@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,700;1,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:wght@700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  --black: #161716;
  /* --neon: #dafdf7; */
  /* --neon: #15ccdc; */
  --neon-blue: #7ae4e0;
  --neon-orange: #e4a25d;
  --neon-yellow: #dfd24e;
  --neon-green: #7fdda4;
}

body {
  background-color: var(--black);
  /* color: var(--neon-blue); */
  /* color: #15ccdc; */
  color: white;
  font-family: "Source Code Pro", monospace;
  text-shadow: 0 0 5px var(--neon-blue);
  font-size: 1.5em;
}

.container {
  border: 4px solid var(--neon-blue);
  border-radius: 5px;
  padding: 5px;
  margin: 10px;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: var(--neon-blue);
  border-top: 2px solid var(--neon-blue);
  border-bottom: 2px solid var(--neon-blue);
  margin: 10px 20px;
  padding: 5px 10px;
}

.introduce {
  display: flex;
  align-items: center;
}

.introduce h1 {
  display: inline-block;
  font-family: "Exo", sans-serif;
  font-size: 1em;
  border: 2px solid var(--neon-blue);
  border-radius: 5px;
  text-transform: uppercase;
  padding: 5px;
  margin-right: 10px;
}

.introduce h2 {
  font-size: 0.625em;
  color: var(--neon-orange);
}

.bio {
  display: flex;
  align-items: center;
}

.bio p {
  margin-left: 10px;
}

.bio-image {
  width: 200px;
  height: 200px;
}

.img-container {
  border: 5px groove var(--neon-blue);
  border-radius: 5px;
  margin: 10px;
}

